/* Style the tables */
.styled-table {
    border-collapse: collapse;
    margin: 25px 0;
    font-size: 1em;
    min-width: 400px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

/* Sticky table header*/
.styled-table th {
    position: sticky;
    top: 0;
    background-color: #496e67;
    color: #ffffff;
}

.styled-table thead tr,  .styled-table tfoot tr{
    background-color: #496e67;
    color: #ffffff;
    text-align: left;
}

.styled-table th,
.styled-table td {
    padding: 5px 5px;
    border-right: 1px solid #575252;
}

/* Prevent collapse of empty rows*/
.styled-table td:empty::after{
    content: "\00a0";
}


.styled-table tr .cl-yes {
    background-color: #239B56;
}

.styled-table tr .cl-no {
    background-color: #B03A2E;
}

.styled-table tr .cl-maybe {
    background-color: #FFA500;
}


/* .styled-table tbody tr {
